export default {
	data() {
		return {
			order: {},
			orderId: 0
		}
	},
	onLoad(params) {
		let _this = this;
		_this.orderId = params.orderId;
		_this.request({
			url: _this.http.orderDetail,
			data: {
				orderId: params.orderId
			},
			success: (res) => {
				if (res.code === '0') {
					_this.order = res.result;
				} else {
					uni.showToast({
						title: res.message,
						icon: 'none'
					});
				}
			}
		})
	},
	methods: {
		robOrder() {
			let _this = this;
			let driverId = uni.getStorageSync("userInfo").driverId;
			_this.request({
				url: _this.http.orderTaking,
				data: {
					orderId: _this.orderId,
					driverId: driverId
				},
				success: (res) => {
					if (res.code === '0') {
						uni.showToast({
							title: "抢单成功",
							icon: 'success',
							mask: true
						});
						setTimeout(() => {
							uni.redirectTo({
								url: '/pages/robSuccess/robSuccess?orderId=' + _this.orderId
							});
						}, 1500);
					} else {
						uni.showToast({
							title: res.message,
							icon: 'none'
						});
					}
				}
			});
		}
	}
}
